import React from "react";
import { Select } from "antd"


export default function VariableTypeSelect(props) {
    return (
        <Select
            {...props}
            defaultValue={"System.String"}
            style={{ width: "100%" }}
        >
            <Select.Option value={"System.String"}>String</Select.Option>
            <Select.Option value={"System.Boolean"}>Bool</Select.Option>
            <Select.Option value={"System.Int32"}>Integer</Select.Option>
            <Select.Option value={"System.Collections.ArrayList"}>Array</Select.Option>
        </Select>
    );
}
